import { BROWSER } from '@dhruv-techapps/core-common'

/**
 * do set react chrome extension id in global window object
 */

const CHROME_ERROR = 'window.chrome not found'
export class Service {
  static messageChrome(message) {
    return new Promise((resolve, reject) => {
      if (!window.chrome) {
        reject(CHROME_ERROR)
        return
      }
      const responseFunc = response => {
        if (window.chrome.runtime.lastError || response.error) {
          reject(window.chrome.runtime.lastError || response.error)
        } else {
          resolve(response.result)
        }
      }
      if (window.react_env) {
        window.chrome.runtime.sendMessage(window.react_env[`REACT_APP_${BROWSER}_EXTENSION_ID`], message, responseFunc)
      } else {
        window.chrome.runtime.sendMessage(message, responseFunc)
      }
    })
  }

  static async message(message) {
    return await this.messageChrome(message)
  }
}
