import { MANIFEST_ACTIONS, RUNTIME_MESSAGE } from '@dhruv-techapps/core-common'
import { Service } from './service'

export class ManifestService extends Service {
  static async values(keys) {
    return await this.message({ action: RUNTIME_MESSAGE.MANIFEST, manifestAction: MANIFEST_ACTIONS.VALUES, keys })
  }

  static async value(key) {
    return await this.message({ action: RUNTIME_MESSAGE.MANIFEST, manifestAction: MANIFEST_ACTIONS.VALUE, key })
  }
}
