/* eslint no-console: off */
/* global mode */

const LOGGER_DEBUG_REGEX = /_dev|__local/
export class Logger {
  static log(...args) {
    console.log.apply(null, [chrome.runtime.getManifest().name, ...args])
  }

  static warn(...args) {
    console.warn.apply(null, [chrome.runtime.getManifest().name, ...args])
  }

  static error(...args) {
    console.error.apply(null, [chrome.runtime.getManifest().name, ...args])
  }

  static info(...args) {
    console.info.apply(null, [chrome.runtime.getManifest().name, ...args])
  }

  static debug(...args) {
    if (LOGGER_DEBUG_REGEX.test(mode.env)) {
      console.debug.apply(null, [chrome.runtime.getManifest().name, ...args])
    }
  }
}
